<template>
	<div id="myPage">
		<div class="title_wrap">
			<p class="title">마이페이지</p>
			<div class="user_img">
				<img :src="`${baseImageUrl}${mydata.AV_EMP_NO}`" alt="" />
			</div>
		</div>
		<div class="user_info">
			<dl class="name">
				<dd>이름 / ID</dd>
				<dt>{{ mydata.NAME }} ({{ mydata.AV_EMP_NO }})</dt>
			</dl>
			<dl class="left_AV_days">
				<dd>남은 연차</dd>
				<dt>{{ getLeftDays() }}</dt>
			</dl>
			<dl class="work_years">
				<dd>근무 연수</dd>
				<dt>{{ mydata.easyday }}</dt>
			</dl>
			<dl class="refresh_AV_days">
				<dd>연차 갱신일</dd>
				<dt>{{ mydata.AV_REGDATE | formatFromMonth }}</dt>
			</dl>
		</div>
		<button @click="logout" class="logout_btn">logout</button>
	</div>
</template>

<script>
import { fetchMypage } from "@/api/index";

export default {
	data() {
		return {
			baseImageUrl: process.env.VUE_APP_IMG_URL,
		};
	},
	computed: {
		mydata() {
			return this.$store.state.arrMypageList;
		},
	},
	methods: {
		getLeftDays() {
			const leftDays = this.mydata.AV_DAYS - this.mydata.AV_USE_DAYS;
			return leftDays;
		},
		logout() {
			if (confirm("로그아웃 하시겠습니까?")) {
				localStorage.clear();
				this.$store.commit("clearAccessToken");
				this.$store.commit("clearRefreshToken");
				this.$router.push("/login");
			}
		},
	},
	async created() {
		try {
			const mydata = await fetchMypage();
			this.$store.commit("setmypageList", mydata.data);
		} catch (error) {
			// console.log(error);
		}
	},
};
</script>

